.flexMa {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 20px;
  text-align: center;
  justify-content: center;
  margin: auto;
  
}
.M1,
.M2,
.M3,
.M {
  width: 30vw;
  background-color: rgb(69, 69, 69);
  padding: 30px;
}

.M p {
  border: 5px solid;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  padding: 30px;
}
