.Header p {
  font-size: 72px;
  background: -webkit-linear-gradient(
    110deg,
    rgba(41, 3, 223, 1) 30%,
    rgba(0, 249, 73, 1) 80%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.Header a {
  color: white;
  font-size: 30px;
}

.Header .flexH {
  display: flex;
  align-items: baseline;
  justify-content: space-around;
}

@media screen and (max-width: 700px) {
  .Header .flexH {
    display: flex;
    flex-direction: column;
 
  }

  .Header p{
    font-size: 40px;
    margin-bottom: 20px;
  }
}
